.App {
    text-align: center;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  .del-btn {
    margin-top: 3px;
    width: 20px;
    font-size: xx-large;
    transition-duration: 0.5s;
  }
  
  .del-btn:hover {
    color: red;
    cursor: pointer;
  }
  
  *{
    /*outline: 1px solid rgba(255, 0, 0, 0.2);*/
  }
  
  .menuIcon {
    margin-top: 0px;
    font-size: 16px;
    text-decoration:none;
  }
  
  .hide {
    display: none;
  }
  
  .unselectable {
    user-select: none;
  }
  
  .disable-select {
  pointer-events: none
  }
  
  .active {
    color: #F00;
  }

  .btn-text {
    flex: 1;
    margin: 0 3px;
  }

  .fc {
    justify-content: center;
    align-items: center;
  }

  .f {
    display: flex;
  }